<script>
import { mapState, mapActions } from 'vuex';

export default {
  name: 'ShowBody',
  computed: {
    ...mapState(['product'])  // 從 Vuex state 中獲取產品數據
  },
  methods: {
    ...mapActions(['updateCurrentImage']),  // 映射 Vuex 中的 action
    viewImage(product) {  // 點擊時傳遞整個 product
      this.updateCurrentImage({ img1: product.img1, alt: product.name });  // 更新圖片和 alt
      this.$router.push('/page');  // 跳轉到 Page.vue 頁面
    },
    btnclick (){
      this.$router.push('/car')
    }
  }
};
</script>

<template>
  <div class="Sbody">
    <div class="item" v-for="(product, index) in product" :key="product.id">
      <span><h3>{{ product.name }}</h3></span>
      <div @click="viewImage(product)"> <!-- 傳遞整個 product -->
        <img :src="product.img1" :alt="product.name" />
      </div>
      <button v-if="product.price" class="btn" @click="btnclick">
        價格: {{ product.price.toLocaleString() }} 美金/個
      </button>
    </div>
  </div>
</template>


<style>
.Sbody {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  justify-items: center;
  align-items: center;
  max-width: 100%;
  overflow: hidden;
  /* border: 1px solid; */
}

.Sbody img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  cursor: pointer;
}

.item {
  /* border: 2px solid #000000; */
  border-radius: 10px;
  background-color: #edd58d;
  word-wrap: break-word;
  overflow-wrap: break-word;
  word-break: break-all;
  white-space: normal;
  text-align: center;
  padding: 10px;
  width: 300px;
  /* border: 1px solid; */
}

.btn {
  height: 3rem;
  font-size: 15px;
  margin-top: 5px;
  width: 8rem;
  cursor: pointer;
}
@media (max-width: 768px) {
    .Sbody{
        flex-direction: column;
        margin: 1px;
        padding: 1px;
    }
}
</style>
